@font-face {
  font-family: "Video400";
  src: url("../fonts/Video-Regular.ttf");
}
@font-face {
  font-family: "Video300";
  src: url("../fonts/Video-Light.ttf");
}
@font-face {
  font-family: "WorkSans";
  src: url("../fonts/WorkSans.ttf");
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  color: #020206;
}

body {
  height: fit-content;
}

html {
  scroll-behavior: smooth;
  height: fit-content;
}

.container {
  width: 1326px;
  margin: 0 auto;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.checkbox-container {
  display: flex;
  align-items: center;
}
.checkbox-container .checkbox {
  display: block;
  position: relative;
  height: 48px;
  width: 48px;
  cursor: pointer;
  user-select: none;
  flex-shrink: 0;
  /* Hide the browser's default checkbox */
  /* Create a custom checkbox */
  /* On mouse-over, add a background color */
  /* When the checkbox is checked, add a background */
  /* When the checkbox is checked, add a background */
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Show the checkmark when checked */
  /* Style the checkmark/indicator */
}
.checkbox-container .checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
}
.checkbox-container .checkbox .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  border-radius: 50%;
  border: 2px solid #45D5EC;
}
.checkbox-container .checkbox .checkmark.error {
  border: 1px solid blue;
  background-color: rgba(255, 80, 80, 0.0705882353);
}
.checkbox-container .checkbox .checkmark.error::before {
  content: "!";
  top: 43%;
  left: -13px;
  position: absolute;
  transform: translateY(-50%);
  font-size: 30px;
  color: blue;
  font-weight: bold;
}
.checkbox-container .checkbox input:checked ~ .checkmark {
  background-color: #45D5EC;
  border: 2px #45D5EC solid;
}
.checkbox-container .checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.checkbox-container .checkbox input:checked ~ .checkmark:after {
  display: block;
}
.checkbox-container .checkbox .checkmark:after {
  left: 17px;
  top: 10px;
  width: 7px;
  height: 17px;
  border: solid #fff;
  border-width: 0 4px 4px 0;
  transform: rotate(45deg);
}
.checkbox-container label {
  cursor: pointer;
}
.checkbox-container .label-text {
  display: inline-block;
  font-size: 18px;
  cursor: pointer;
  margin: 0 15px;
}

.form-pages {
